export default {
	
	data() {
		return {
			keyWord:"",	//搜索关键字
			currentLoadFlag:true,
			newsList: [],	//全部宝贝、新品、活动数组集合
			storeId: '85',	// 本店ID
			storeInfo: {
				merInfo:{
					views:0
				}
			},	//店铺首页
			tabIndex: 0,	//当前下标
			scrollInto: '',	//scroll跳转到这个Id
			isKeep: false,  //收藏
			tabBars: [
				{
					title: '全部宝贝',
					id: 'baobei'
				},
				{
					title: '热卖商品',
					id: 'remai'
				},
				{
					title: '新品上架',
					id: 'xingping'
				},
				{
					title: '活动促销',
					id: 'huodong'
				}
			],
		};
	},
	onLoad(e) {
		this.storeId = e.storeId;
		this.getStoreInfo();
		this.initInfo();
		this.getGoodList(0);
	},
	onPullDownRefresh() {
		//下拉刷新页面将之前的数据初始化
		this.newsList = [];
		this.getStoreInfo();
		this.initInfo();
		this.getGoodList(0);
	},
	computed:{
		scrollerHeight:function(){
			return (this.WindowHeight-320/2)+'px';	//16是调试出来的差值
		}
	},
	methods: {
		//初始化newsList数据类型
		initInfo() {
			this.tabBars.forEach(tabBar => {
				this.newsList.push({
					data: [],
					loadingStatus:"loading",
					currentPage: 1,
					pageSize: 6,
					loadingFlag:true,
				});
			});
		},
		// 加载更多， 
		onReachBottom_more() {
		    if(this.newsList[this.tabIndex].loadingStatus === 'loading'){	//防止重复加载
				return ;
			}
			this.newsList[this.tabIndex].loadingFlag = true;
			this.newsList[this.tabIndex].loadingStatus='loading';	//每次上滑都要从新设置status
			setTimeout(()=>{
				this.getGoodList(this.tabIndex);	
			},600) 
		},
		//导航栏点击跳转
		ontabtap(e) {
			let index = e.target.dataset.current || e.currentTarget.dataset.current;
			this.tabIndex = index;
		},
		//左右滑动
		ontabchange(e) {
			let index = e.target.current || e.detail.current;
			this.tabIndex = index;
			this.scrollInto = this.tabBars[index].id;
			//获取信息
			if (this.newsList[index].data.length === 0) {
				this.getGoodList(index);
			}
		},
		//0.8s后隐藏加载提示的信息
		closeLoadingMoreFlag() {
			setTimeout(()=> {
				this.newsList[this.tabIndex].loadingFlag = false;
			}, 800);
		},
		//获取全部宝贝、新品、活动促销
		getGoodList(index) {
			let that = this;
			// 如果是首页点击那么不需要请求
			// if (index == 0) return;
			that.baseGet(
				that.U({
					c: 'store_api',
					a: 'get_product_list',
					q: {
						mid: that.storeId,
						hot:index==1 ? 1 : '',
						news: index == 2 ? 1 : '',
						benefit: index == 3 ? 1 : '',
						// news: index == 2 ? 1 : '',
						// benefit: index == 3 ? 1 : '',
						page: that.newsList[index].currentPage,
						limit: that.newsList[index].pageSize
					}
				}),
				function(res) {
					uni.stopPullDownRefresh();	
					if (res.data.length == 0) {
						that.newsList[index].loadingStatus = 'noMore';
						that.closeLoadingMoreFlag();	//0.8后将状态隐藏
						return;
					}
									
					that.newsList[index].data = [...that.newsList[index].data, ...res.data];
					that.newsList[index].currentPage++;
					that.newsList[index].loadingStatus="more";
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		//获取店铺详情
		getStoreInfo() {
			let that = this;
			that.baseGet(
				that.U({
					c: 'merchant_api',
					a: 'merchant_info',
					q: {
						id: that.storeId
					}
				}),
				function(res) {
					that.storeInfo = res.data;
					that.isKeep = res.data.merInfo.is_collect;
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		//收藏
		keep() {
			let that = this;
			//收藏商品之前判断用户是否已经登录
			if (this.isLogin()) {
				if (this.isKeep != null) {
					//收藏的情况下，进行取消收藏操作
					that.basePost(
						that.U({ c: 'merchant_api', a: 'uncollect_merchant' }),
						{
							merId: that.storeId
						},
						function(res) {
							if(res.code==200){
								that.isKeep = null;
							}
						},
						function(res) {
							console.log(res);
						}
					);
				} else {
					//还没有收藏的情况下，进行收藏操作
					that.basePost(
						that.U({ c: 'merchant_api', a: 'collect_merchant' }),
						{
							merId: that.storeId
						},
						function(res) {
							if(res.code==200){
								that.isKeep = '1';
							}
						},
						function(res) {
							console.log(res);
						}
					);
				}
			}
		},

		// 商品详情
		detail(e) {
			if(e.type==2){
				uni.navigateTo({
					url: '/pages/list/line_details/line_details?id='+e.id
				});
			}else{
				uni.navigateTo({
					url: '/pages/list/goode_details/goode_details?id='+e.id
				});
			}
			
		},
	}
};
